import React from 'react'
import styled from 'styled-components'
import EmailBg from '../assets/images/emailbg.jpg'
import { Button } from './Button'

function Email() {
    return (
        <EmailContainer id="contact">
            <EmailContent>
                <h1>Entre em contato conosco</h1>
                <p>Preencha o formulário e entraremos em contato</p>
                <form action="#">
                    <FormWrap>
                        <label htmlFor="nome">
                            <input type="text" placeholder="Seu nome" id="name" />
                            <input type="email" placeholder="Seu e-mail" id="email" />
                        </label>
                        <Button 
                            as='button' 
                            type='submit' 
                            primary="true" 
                            css={`
                                height: 48px;

                                @media screen and (max-width: 768px) {
                                    width: 100%;
                                    min-width: 350px;
                                }

                                @media screen and (max-width: 400px) {
                                    width: 100%;
                                    min-width: 250px;
                                }
                            `}
                        >
                            Enviar
                        </Button>
                    </FormWrap>
                </form>
            </EmailContent>
        </EmailContainer>
    )
}

export default Email

const EmailContainer = styled.div`
    background: linear-gradient(180deg, rgba(22,40,54,0.6) 0%, rgba(22,40,54,0.6) 35%, rgba(22,40,54,0.1) 100%), url(${EmailBg}) no-repeat center;
    background-size: cover;
    height: 450px;
    width: 100%;
    padding: 5rem calc((100vw - 1300px) /2);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
`
const EmailContent = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        text-align: center;
        margin-bottom: 0.5rem;
        font-size: clamp(1.3rem, 5vw, 3rem);
        padding: 0 1rem;
    }

    p {
        text-align: center;
        font-size: clamp(1rem, 2.5vw, 1.5rem);
        padding: 0 1rem;
        margin-bottom: 2rem;
    }


`
const FormWrap = styled.div`
    input{
        padding: 1rem 1.5rem;
        outline: none;
        width: 350px;
        height: 48px;
        border: none;
        margin-right: 1rem;
    }

    @media screen and (max-width: 768px) {
        display: flex;
        flex-direction: column;
        padding: 0 1rem;

        input {
            margin-bottom: 1rem;
            width: 100%;
            margin-right: 0;
        }
    }


`
